import { DateObj } from "dayzed";
import styled from "styled-components";

import { colors, spacingMap } from "../styleguide/";

import { RangePositions } from "./DateButton";

type CalendarDateButtonWrapperProps = {
	buttonSelected?: RangePositions;
	isSelected: boolean,
	isToday?: boolean,
};

export type CalendarDateButtonProps = Pick<DateObj, "selected"> & {
	isInRange?: boolean
};

const calendarDateButtonBackground = `
	background: ${colors.subduedBlueHover};
`;

export const CalendarDateButtonWrapper = styled.div<CalendarDateButtonWrapperProps>`
	${({ buttonSelected, isSelected }) => {
		if (!isSelected) {
			return;
		}

		if (buttonSelected === "rangeStart") {
			return `
				border-radius: 30px 0px 0px 30px;
				${calendarDateButtonBackground};
			`;
		}

		if (buttonSelected === "rangeEnd") {
			return `
				border-radius: 0px 30px 30px 0px;
				${calendarDateButtonBackground};
			`;
		}
		return;
	}}
`;

export const StyledSvg = styled.svg`
	position: absolute;
	bottom: ${spacingMap.xxs};
`;

export const CalendarDateButton = styled.button<CalendarDateButtonProps>`
	width: 40px;
	height: 40px;
	background: transparent;
	padding: 0;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: relative;
	border: 2px transparent solid;

	${({ isInRange }) => isInRange && `
		background: ${colors.subduedBlueHover};
		> span {
			color:  ${colors.midBlue};
		}
	`}

	${({ selected }) => selected ? `
		background: ${colors.midBlue};
		border-radius: 30px;
		&:focus-visible {
			position: relative;
			outline: none;
			&:after {
				content: "";
				position: absolute;
				border: 2px solid ${colors.midBlue};
				width: 40px;
				height: 40px;
				z-index: 2;
				box-sizing: border-box;
			}
		}

		> span {
			color: ${colors.white};
		}
		` : `
		&:focus {
			border: 2px ${colors.midBlue} solid;
			outline: none;
		}

		&:hover {
			background: ${colors.subduedBlueHover};
		}

		&:active {
			border: 2px transparent solid;
			outline: none;
		}
	`}

	&:hover {
		cursor: pointer;
	}

	&:disabled {
		opacity: 33%;
		cursor: not-allowed;

		&:hover {
			background: initial;

			> span {
				color: initial;
			}
		}
	}
`;
